body {
    background-color: hsl(200, 100%, 90%);
    color: coral;
    padding: 1rem;
    margin: 0 auto;
}

hr {
    border: 1px solid coral;
    width: 100%;
}

img {
    width: 50px;
    height: 50px;
}

.btn {
    background: hsla(200, 100%, 50%, 0.1);
    border: 1px solid hsl(200, 100%, 50%);
    color: hsl(200, 100%, 50%);
    padding: 0.25em 0.5em;
    border-radius: 0.25em;
    cursor: pointer;
    outline: none;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.items-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.books-container {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    width: 100%;
}

.filters {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    width: 100%;
}

.filter{
    background: rgb(253, 177, 149);
    border: 1px solid coral;
    color: azure;
    padding: 0.25em 0.5em;
    border-radius: 0.25em;
    cursor: pointer;
    outline: none;
}

.book-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: azure;
    border: 2px solid coral;
    padding: 1rem;
    border-radius: 8px;
    width: 200px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
